/* App.js uses this css file (what about App.css file?) */
/* bootstrap link (for buttons) is in index.html in public folder of project */
body{
  background-color: #282c34;
}
#board{
  margin-top:3.5%;
  padding-left: 16%;
  display:inline-block;
}

#miniBoard{
  margin-top:17%;
  display:block;
}

.miniSquare{ /*goal: make glowy*/
  border-radius:2px;
  background-color: #f1fbff;
  border: 1px solid #023e56;
  height: 60px;
  width: 60px;
  font-size: 20px;
  /* font-weight: bold; */
  text-align: center;
  transition: all 0.2s ease-in-out;
}

.miniSquare:hover{
  background-color: rgb(8, 206, 255);
  box-shadow: 10px 10px 11px rgba(0, 234, 255, 0.4);
}

#rectangle{
  padding:10%;
  outline:3px solid black;
  border-radius:2px;
  margin-left:2%;
  padding-left:19%;
  padding-right:19%;
  text-align: center;
  font-family:Arial, Helvetica, sans-serif;
  background-image: url("colorsplash.jpg");
  background-size:100%;
  color:white;
  font-size:16px;
}

#buttons{
  margin-top:0;
  padding-left:2%;
  display:inline-block;
}

.sBtn{
  margin-right: 1%;
  width:100%;
  display:block;
  margin-top:5%;
  margin-bottom:5%;
}

.rBtn{
  margin-left: 0%;
  margin-right: 1%;
  width:100%;
  display:block;
  margin-top:5%;
  margin-bottom:5%;
}

.cBtn{
  width:100%;
  display:block;
  margin-top:5%;
}

.eBtn{
  width:100%;
  padding: 7px 10px 7px 10px;
}

.pBtn{
  width:100%;
  padding: 7px 10px 7px 10px;
}

.insBtn{
  width:100%;
  padding:7px 10px 7px 10px;
  margin:0;
}

#sbs-buttons{ /*display container for custom, set #'s and instructions buttons*/
  display:flex;
  margin-top:5%;
  text-align:right;
}

.square{ /*filled (standard) squares, i.e. blank squares in the original generated puzzles; values are fixed*/
  background-color: #fafafa;
  border: 1px solid #303030;
  height: 54.7px;
  width: 54.7px;
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}

.emptySquare{ /*filled square but with some modifications to differentiate from filled squares*/
  background-color: #fafafa;
  border: 1px solid #303030;
  height: 54.2px;
  width: 54.2px;
  font-size: 30px;
  text-align: center;
}

.redSquare{
  background-color: #fccbc7;
  border: 1px solid #303030;
  height: 54.2px;
  width: 54.2px;
  font-size: 30px;
  text-align: center;
}

.greenSquare{
  background-color: #c2ffd2;
  border: 1px solid #303030;
  height: 54.7px;
  width: 54.7px;
  font-size: 30px;
  text-align: center;
  font-weight: bold;
}

#leftBorder{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 16%;
}

#topBorder{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 6.25%;
}

#bottomBorder{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 85%;
}

#rightBorder{
  border-left: 6px solid black;
  height: 495px;
  position:absolute;
  left:54%;
}

#verticalOne{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 28.5%;
}

#verticalTwo{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 41.5%;
}

#horizontalOne{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 32.5%;
}

#horizontalTwo{
  border-top: 6px solid black;
  width: 492.6px;
  position: absolute;
  top: 59%;
}

.hDivider{
  margin-right:10px;
}

/*css for removing the js default scrollwheel for input type numbers*/
input[type="number"]::-webkit-outer-spin-button, 
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}



